<script>
  import {onMount,createEventDispatcher} from 'svelte'
  import {fade} from 'svelte/transition'
  import CodeMirror from 'codemirror/lib/codemirror.js'
  import 'codemirror/mode/javascript/javascript.js'
  import 'codemirror/mode/handlebars/handlebars.js'
  import 'codemirror/mode/xml/xml.js'
  import 'codemirror/mode/css/css.js'

  import emmet from '@emmetio/codemirror-plugin';
  emmet(CodeMirror);

  // import 'codemirror/lib/codemirror.css'

  import 'codemirror/addon/selection/active-line.js'
  import 'codemirror/addon/comment/comment.js'
  import 'codemirror/addon/fold/foldcode.js'
  import 'codemirror/addon/fold/xml-fold.js'
  import 'codemirror/keymap/sublime.js'

  const dispatch = createEventDispatcher()

  export let prefix = ''
  export let value = ''
  export let mode = {
    name: 'handlebars',
    base: 'text/html'
  }
  export let disabled = false
  export let style = ''
  export let CodeMirrorOptions = {}
  export let docs = null

  var Editor

  onMount(() => {
    Editor = CodeMirror(editorNode, {
      // passed values
      value: prefix + value,
      mode,
      readOnly: disabled,
      ...CodeMirrorOptions,
      // set values
      theme: 'vscode-dark',
      lineNumbers: true,
      gutter: true,
      indentUnit: 4,
      smartIndent: false,
      styleActiveLine: {nonEmpty: true},
      styleActiveSelected: true,
      keyMap: 'sublime',
      extraKeys: {
        'Tab': 'emmetExpandAbbreviation',
        'Esc': 'emmetResetAbbreviation',
        'Enter': 'emmetInsertLineBreak'
      },
      emmet: {
        previewOpenTag: false
      }
    });
    setTimeout(() => {Editor.refresh()}, 500)

    Editor.on('change', () => {
      const newValue = Editor.doc.getValue()
      value = newValue.replace(prefix, '')
      dispatch('change')
    })
    Editor.on("gutterClick", foldHTML);
    function foldHTML(cm, where) { console.log(cm, where); cm.foldCode(where, CodeMirror.tagRangeFinder); }
  })

  $: {
    if (Editor) {
      // make bind:value work from parent to child
      if ((prefix+value) !== Editor.doc.getValue()) {
        Editor.getDoc().setValue(prefix+value)
      }
      // make `disabled` dynamic
      Editor.setOption('readOnly', disabled)
    }
  } 

  let editorNode

</script>

<div class="codemirror-container" style="{style}">
  <div in:fade={{ duration: 200 }} bind:this={editorNode}></div>
  {#if docs}
    <a in:fade={{ duration: 200 }} target="blank" href="{docs}" class="z-10 text-xs pointer-events-auto flex items-center absolute bottom-0 right-0 h-auto text-gray-100 py-1 px-3 m-1 bg-gray-900 hover:bg-primored transition-colors duration-200">
      <i class="fas fa-external-link-alt mr-1"></i>
      <span>Docs</span>
    </a>
  {/if}
</div>

