<script>
  import {onMount} from 'svelte'
  import store from 'store2'
  import {site,settings as settingsStore,pageData} from '@stores/data'

  export let multiple = false
  export let pages = null
  export let id = null
  export let html = ''
  export let css = ''
  export let js = ''
  export let settings = null
  export let dependencies = null
  export let loading = false;
  export let view = null

  export let includeParentStyles = false

  $: if (includeParentStyles) {
    css = $site.styles.final + $pageData.styles.final + css
  }

  var bc = new BroadcastChannel('preview');

  let ready = false
  bc.onmessage = () => {
    ready = true
  }

  $: multiple && ready && bc.postMessage(pages)

  $: store.set('preview', { 
    id,
    html,
    css,
    js,
    settings,
    dependencies
  })

  let iframe

  let timeout
  let cachedJs = js // reload the iframe whenever js changes to wipe the page's memory
  $: if (cachedJs !== js) {
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      iframe.contentWindow.location.reload()
    }, 1000)
  }

  let container
  let iframeLoaded = false

  function resizePreview() {
    if (view) {
      const {clientWidth:parentWidth} = container
      const {clientWidth:childWidth} = iframe
      const scaleRatio = parentWidth / childWidth
      iframe.style.transform = `scale(${scaleRatio})`
      iframe.style.height = (100 / scaleRatio) + '%'
    }
  }

  function changeView() {
    iframe.classList.remove('fadein')
    setTimeout(() => {
        if (view === 'small') {
          view = 'large'
          // clientWidth doesn't compute right without this
          setTimeout(resizePreview, 100)
        } else {
          iframe.style.transform = 'scale(1)'
          iframe.style.height = '100%'
          view = 'small'
        }

      setTimeout(() => {
        iframe.classList.add('fadein')
      }, 400)

    }, 100)
  }

  onMount(resizePreview)

</script>

<div class="h-full flex flex-col pl-2">
  <div class="preview-container flex-1" class:loading bind:this={container}>
    <iframe class:scaled={view === 'large'} on:load={() => {iframeLoaded = true}} class:fadein={iframeLoaded} title="Preview HTML" src="preview.html" class="bg-white w-full h-full" bind:this={iframe}></iframe>
  </div>
  <div class="footer-buttons">
    {#if view}
      <button class="switch-view" on:click={changeView}>
        change view
      </button>
    {/if}
    <a target="blank" class="separate-tab" href="/?preview={multiple ? 'multiple' : true}">
      <span>preview in separate tab</span>
      <span class="icon ml-1">
        <i class="fas fa-external-link-alt"></i>
      </span>
    </a>
  </div>
</div>

<svelte:window on:resize={resizePreview} />

<style>iframe {
  opacity: 0;
  width: 100%;
  transition-property: opacity;
  transition-duration: 100ms;
}

iframe.scaled {
  width: 100vw;
  transform-origin: top left;
}

.fadein {
  opacity: 1;
  transition-duration: 200ms;
}

.preview-container {
  will-change: border-color;
  border-width: 4px;
  border-style: solid;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 500ms;
  overflow: hidden;
  border-bottom: 0;
}

.preview-container.loading {
  border-color: rgb(248,68,73);
}

.preview-container.loading + .preview-html {
  --bg-opacity: 1;
  background-color: rgb(248,68,73);
  background-color: rgba(248, 68, 73, var(--bg-opacity));
  --text-opacity: 1;
  color: #fff;
  color: rgba(255, 255, 255, var(--text-opacity));
}

.footer-buttons  {
  display: flex;
}

.footer-buttons a, .footer-buttons button {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  outline: 0;
  width: 100%;
  --bg-opacity: 1;
  background-color: #edf2f7;
  background-color: rgba(237, 242, 247, var(--bg-opacity));
  --text-opacity: 1;
  color: #718096;
  color: rgba(113, 128, 150, var(--text-opacity));
  font-size: 0.75rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  display: block;
  text-align: center;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 500ms;
}

.footer-buttons a:hover, .footer-buttons button:hover {
  --bg-opacity: 1;
  background-color: #cbd5e0;
  background-color: rgba(203, 213, 224, var(--bg-opacity));
}

.switch-view {
  border-right-width: 1px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity));
}

.separate-tab {
  border-left-width: 1px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity));
}</style>